/**
 * Utility functions for user profile management
 */

import { InputType } from './use-passive-sensing'

// Mapping from onboarding InputType to app input mode IDs
export const INPUT_TYPE_TO_MODE_MAP: Record<InputType, string[]> = {
    'voice': ['voice'],
    'motion': ['head-motion'],
    'switch': ['switch'],
    'blink': ['eye-gaze'], // Blink detection falls under eye gaze
    'gaze': ['eye-gaze'],
    'head': ['head-motion'],
    'sign': ['sign'],
    'facial': ['facial'],
    'sip-puff': ['sip-puff'],
    'none': []
}

export interface UserProfile {
    name: string
    primaryInput: InputType
    availableInputs: InputType[]
    lastUsed: string
    calibration: {
        primaryInput: InputType
        availableInputs: InputType[]
        precision: 'low' | 'medium' | 'high'
        tremorDetected: boolean
        dwellTime: number
        rangeOfMotion: number
        preferredSpeed: 'slow' | 'medium' | 'fast'
    }
}

/**
 * Get the current user profile from localStorage
 */
export function getCurrentProfile(): UserProfile | null {
    if (typeof window === 'undefined') return null
    
    const profileStr = localStorage.getItem('fluent-current-profile')
    if (!profileStr) return null
    
    try {
        return JSON.parse(profileStr)
    } catch {
        return null
    }
}

/**
 * Get the list of enabled input mode IDs based on the user's profile
 * Returns all modes if no profile is set (for first-time users)
 */
export function getEnabledInputModes(): string[] {
    const profile = getCurrentProfile()
    
    // If no profile, allow all modes (user hasn't completed onboarding)
    if (!profile || !profile.availableInputs || profile.availableInputs.length === 0) {
        return ['voice', 'draw', 'haptic', 'type', 'sign', 'eye-gaze', 'head-motion', 
                'facial', 'sip-puff', 'switch', 'board', 'custom']
    }
    
    // Map detected inputs to mode IDs
    const enabledModes = new Set<string>()
    
    for (const inputType of profile.availableInputs) {
        const modes = INPUT_TYPE_TO_MODE_MAP[inputType] || []
        modes.forEach(mode => enabledModes.add(mode))
    }
    
    // Always enable these universal modes
    enabledModes.add('board') // Communication board works for everyone
    enabledModes.add('custom') // Custom input is always available
    enabledModes.add('type') // Keyboard is universal
    enabledModes.add('draw') // Drawing is universal
    
    return Array.from(enabledModes)
}

/**
 * Check if a specific input mode is enabled for the current user
 */
export function isInputModeEnabled(modeId: string): boolean {
    const enabledModes = getEnabledInputModes()
    return enabledModes.includes(modeId)
}

/**
 * Get the primary input mode ID based on the user's profile
 */
export function getPrimaryInputMode(): string | null {
    const profile = getCurrentProfile()
    if (!profile) return null
    
    const modes = INPUT_TYPE_TO_MODE_MAP[profile.primaryInput] || []
    return modes[0] || null
}
